
import { defineComponent, onMounted, reactive } from 'vue'
import { useRouter, useRoute } from "vue-router" 
import detailHeader from "@/components/header.vue"
import { getDetail, addComment, getComment, setStarOrCancel } from "@/api/index"
import { Toast } from 'vant'

interface detailProps {
  imgUrl: string;
  nickName: string;
  browse: number,
  title: string,
  content: string
}


export default defineComponent({
  name: 'homeDetail',
  components: {
    detailHeader
  },
  setup(props, context){
    const state = reactive({
      route: "",
      detailObj: {},
      message: "",
      commentList: [{}],
      pageNum: 1,
      pageSize: 3,
      total: 0, // 评论总条数
      num: null, // 获取参数
      numRes: 3 // 总条数
    })
    let router = useRouter()
    let route = useRoute()
    const handleBack = () => {
      router.go(-1)
    }
    onMounted(() => {
      // console.log("9090---", route.query.id)
      getInit()
    })


    //  数据调用 初始化
    const getInit = () => {
      let data = {
        id: route.query.id
      }
      // 获取到图片数据
      getDetail(data).then(res => {
        // console.log("res", res)
        if(res.code == 200){
          state.detailObj = res.data.list
        }
      })

      // 获取到评论数据
      let dataRes = {
        pid: route.query.id,
        pageNum: state.pageNum,
        pageSize: state.pageSize
      }

      getComment(dataRes).then(res => {
        // console.log('luts00000', res)
        if(res.code == 200){
          state.commentList = res.data.list
          state.total = res.data.total
        }
      })
    }

    // 提交
    const handleCommit = () => {
      // console.log("handleCommit", state.message)
      if(state.message){
        let data = {
          id: route.query.id,
          content: state.message
        }
        addComment(data).then(res => {
          // console.log("xxx-data", res)
          if(res.code){
            Toast("评论成功")
            getInit()
            state.message = ""
          }
        })
      }else{
        Toast("您还未留言哦")
      }
    }

    // 网友点赞
    const handleStar = (item: any, index: any) => {
      if(item.star){
        item.star = 0
        console.log("====-yes", item.star)
      }else{
        item.star = 1
        console.log("====", item.star)
      }
      let data = {
        id: item.id,
        pid: item.pid
      }

      setStarOrCancel(data).then(res => {
        // console.log("-----", res)
        if(res.code == 200){
          Toast(`${res.msg}`)
          // console.log("9999", state.pageNum)
          // state.pageNum = 1
          // let dataRes = {
          //   pid: route.query.id,
          //   pageNum: state.pageNum,
          //   pageSize: state.numRes
          // }
          // state.commentList = []
          // getComment(dataRes).then(res => {
          //   // console.log('luts00000', res)
          //   if(res.code == 200){
          //     state.commentList = res.data.list
          //     state.total = res.data.total
          //   }
          // })
        }
      })
    }
    // 加载更多
    const handleLoadMore = () => {
      let dataRes = {
        pid: route.query.id,
        pageNum: ++state.pageNum,
        pageSize: state.pageSize
      }
      state.numRes = state.pageNum * state.pageSize
      getComment(dataRes).then(res => {
        // console.log('luts00000', res)
        if(!res.data.list.length){
          Toast("已全部显示了")
          return
        }
        if(res.code == 200){
          state.commentList = [...state.commentList, ...res.data.list]
          state.total = res.data.total
        }
      })
    }
    return {
      state,
      handleBack,
      handleCommit,
      handleStar,
      handleLoadMore
    }
  }
})
